import { Component, OnInit } from '@angular/core';
import { ContactService } from '../contact.service';
import { SubmitContactRequest } from '../request/submit-contact-request';
import { SubmitContactResponse } from '../response/submit-contact-response';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  constructor(private contactService: ContactService) { }

  FullName: string;
  Phone: string;
  Email: string;
  Subject: string;
  Messages: string;
  Id: number;

  ngOnInit() {
  }

  OnSubmit() {
    this.contactService.SubmitContact(new SubmitContactRequest(
      {
        FullName: this.FullName,
        Email: this.Email,
        Phone: this.Phone,
        Subject: this.Subject,
        Messages: this.Messages
      }))
      .subscribe((response) => {
        console.log(response);
        this.Id = response.id;
      });
  }

}
