import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SubmitContactRequest } from './request/submit-contact-request';
import { SubmitContactResponse } from './response/submit-contact-response';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ContactService {

  constructor(private http: HttpClient) { }

  private apiUrl = '//localhost:3000';

  public SubmitContact(request: SubmitContactRequest): Observable<any> {
    return this.http.post(this.apiUrl + '/contacts/',
      request
    );
  }

}
